import { SET_USER_LOGGED_IN, SET_USER_PAGE_STATUS } from "../constants";
const reducer = (state, action) => {
    switch (action.type) {
        case SET_USER_PAGE_STATUS: {
            const userPageStatus = action.payload;
            return Object.assign(Object.assign({}, state), { isActiveUser: userPageStatus.isActiveUser === "1", isLoggedIn: userPageStatus.isLoggedIn, setupCompleted: userPageStatus.setupCompleted, loading: false, pageHash: userPageStatus.pageHash });
        }
        case SET_USER_LOGGED_IN: {
            const isLoggedIn = action.payload;
            return Object.assign(Object.assign({}, state), { isLoggedIn });
        }
        default:
            return state;
    }
};
export { reducer };
